* {
  text-align: center;
}

body {
  padding: 5%;
  @media (min-width: 1000px) {
    margin-left: 15vw;
    margin-right: 15vw;
  }
}

// LINKS MENU
.links-menu {
  display: grid;
  grid-gap: 10px;

  grid-template-columns: auto;

  @media (min-width: 500px) {
    grid-template-columns: auto auto;
  }

  @media (min-width: 1000px) {
    grid-template-columns: auto auto auto auto;
  }
}

.links-menu-item {
  padding: 1%;
  grid-row: auto;
  border: 1px solid black;

  font-size: 5vw;

  @media (min-width: 500px) {
    font-size: 4vw;
  }

  @media (min-width: 1000px) {
    font-size: 2vw;
  }

  h3 {
    font-weight: 400;
  }

  &.selected {
    color: red;
  }

  &:hover {
    cursor: pointer;
  }
}

#link-value {
  margin-top: 2%;
  margin-bottom: 5%;
  padding: 1%;
  border: 1px solid black;
  overflow-x: auto;

  font-size: 5vw;

  @media (min-width: 500px) {
    font-size: 4vw;
  }

  @media (min-width: 1000px) {
    font-size: 2vw;
  }

  a {
    text-decoration: none;
    color: black;
  }

  &:hover {
    a {
      text-decoration: underline;
    }
  }
}

// PROFILE
#profile-picture {
  margin-top: 5%;
  border-radius: 100%;
  border: 3px solid black;

  width: 20vw;
  @media (min-width: 1000px) {
    width: 15vw;
  }
}

#title {
  margin-top: 3%;
  margin-bottom: 1%;

  font-size: 6vw;
  @media (min-width: 1000px) {
    font-size: 4vw;
  }
}

#subtitle {
  margin-bottom: 5%;

  font-size: 5vw;
  @media (min-width: 1000px) {
    font-size: 2vw;
  }

  font-weight: 200;
}

// PORTFOLIO
.heading {
  margin-top: 5%;
}
